import styled, { keyframes } from 'styled-components';

const slideIn = keyframes`
  0% {
    opacity: 0;
    transform: translate(0%, 50%) var(--hover-toolbar-transform);
  }
  90% {
    opacity: 1;
    transform: translate(0%, 5%) var(--hover-toolbar-transform);
  }
  100% {
    opacity: 1;
    transform: translate(0%, 0%) var(--hover-toolbar-transform);
  }
`;


const slideOut = keyframes`
  0% {
    opacity: 1;
    transform: translate(0, 0) var(--hover-toolbar-transform);
  }
  100% {
    opacity: 0;
    transform: translate(0, -50%) var(--hover-toolbar-transform);
  }
`;

export const HOVER_TOOLBAR_SLIDE_IN = 'hover-toolbar-slide-in';
export const HOVER_TOOLBAR_SLIDE_OUT = 'hover-toolbar-slide-out';
export const HOVER_TOOLBAR_NOT_READY = 'hover-toolbar-not-ready';

export const HoverToolbarWrapper = styled.div`
  position: absolute;
  top: 0;
  left: 0;
  background: #FFFFFF;
  border-radius: 6px;
  box-shadow: 0px 8px 24px 0px #00000029, 0px 0px 1px 0px #0000003D;
  opacity: 0;
  cursor: default;
  transform: var(--hover-toolbar-transform);
  z-index: 102;
  .wdn-toolbar-group:first-child {
    padding-left: 6px; // TODO: 围巾组件缺陷，左侧没有padding
  }
  .wdn-toolbar-group:last-child {
    padding-right: 6px; // TODO: 围巾组件缺陷，右侧没有padding
  }
  &.${HOVER_TOOLBAR_SLIDE_IN} {
    animation: ${slideIn} 0.2s ease-out;
  }
  &.${HOVER_TOOLBAR_SLIDE_OUT} {
    animation: ${slideOut} 0.2s ease-out;
  }
  &.${HOVER_TOOLBAR_NOT_READY} {
    opacity: 0;
  }
`;